import React from "react";
import { Link } from "gatsby";

const BlogsData = (data) => {
  const { data: propsData } = data;
  console.log("propsData::", propsData);
  return (
    <section className="section-develop" id="ourteam">
      <div className="container grid">
        <h3 className="bottomAnimation heading-tertiary servicehead">Blogs</h3>
      </div>
      <div className="container grid grid--3-cols margin-top-md">
        <div className="row">
          {propsData?.edges?.map((ele, ind) => {
            const { node } = ele;
            return (
              <div className="col-md-4 ">
                <div
                  key={ind}
                  className="developer card border-0 show p-0 mb-5"
                >
                  <div className="card-header p-0 w-100 border-0">
                    <Link to={`${node.name}`} className="text-decoration-none ">
                      <img
                        src={node.image}
                        alt="Members"
                        className="rounded blog-card-img"
                      />
                    </Link>
                  </div>
                  <div className="card-body">
                    <div className="developer-inner">
                      <div className="d-flex justify-content-between align-items-center">
                        <Link
                          to={`${node.name}`}
                          className="text-decoration-none text-muted"
                        >
                          <h4>{node.name}</h4>
                        </Link>
                        <h5 className="fs-4">{node.language}</h5>
                      </div>
                      <span
                        className="cta-text"
                        dangerouslySetInnerHTML={{
                          __html: node?.description.slice(0, 100),
                        }}
                      ></span>
                    </div>
                  </div>
                  <div className="card-footer border-0 text-end bg-transparent py-3">
                    <Link
                      to={`${node.name}`}
                      className="text-decoration-none  text-muted"
                    >
                      <span className="fs-4 text-decoration-underline">
                        Read more{" "}
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="13"
                          height="13"
                          fill="currentColor"
                          class="bi bi-chevron-right"
                          viewBox="0 0 16 16"
                        >
                            <path
                              fill-rule="evenodd"
                              fontWeight={"bold"}
                              d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"
                            />
                        </svg>
                      </span>
                    </Link>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default BlogsData;
